import React from 'react';
import arrow_1 from './arrow_1.png';
import arrow_2 from './arrow_2.png';
let class_slider= {
'Slider':'slide_Slider__3eX98',
'item1': 'slide_slider_item1__xTG6a',
'item2': 'slide_slider_item2__eFDH2',
'item3': 'slide_slider_item3__1MGiK',
'item4': 'slide_slider_item4__w6Ho7',
'Left':'slide_Left__1Ugu_',
'Right':'slide_Right__1r4Z4',
'Container':'slide_contaner__2doly',
'Arrow1':'slide_Arrow1__1U2EN'
}
const Slide = () => {
	return (
		<div> 
			<div className={class_slider.Left}>
				<div className ={class_slider.Arrow1}>
					<img src={arrow_1}/>
				</div>
			</div>
			<div className={class_slider.Slider}>
				<div className = {class_slider.Container}>
					<div className={class_slider.item1}><b>Сегодня</b></div>
					<div className={class_slider.item2}>Понедельник</div>
					<div className={class_slider.item3}>Вторник</div>
					<div className={class_slider.item4}>Среда</div>
				</div>
			</div>	
			<div className ={class_slider.Right}>
				<div className ={class_slider.Arrow1}>
					<img src={arrow_2}/>
				</div>
			</div>
		</div>
	);
}
export default Slide;